import Player from '../../../models/Player';

const syncCurrentStatus = async (req, res) => {
	try{
		const player = await Player.findOne({ '_id': req.user._id }, 'account.username account.passcode account.role account.activate defaultSetting currentStatus openBetStatus')
		res.json(player)
	}catch(err){
		throw err
	}
}

export default syncCurrentStatus;

